import { Link as RouterLink } from 'react-router-dom';

// hooks
import useSettings from '../hooks/useSettings';
// components
import Page from '../components/Page';
// routes
import { PATH_DASHBOARD } from '../routes/paths';

import { HomeWelcome, HomeTotalMintedNFT, HomeWorkProgress } from '../components/_dashboard/home';
import HomeSectionHeader from '../components/_dashboard/home/HomeSectionHeader';

// status could be: in_progress, completed, to_do
