// ----------------------------------------------------------------------

export default function GeneralApp() {
  const { themeStretch } = useSettings();

  return (
    <Page title="General: App">
      <Container maxWidth={themeStretch ? false : 'xl'}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <HomeWelcome displayName="NFT Lovers" />
          </Grid>

          <Grid item xs={12} md={4}>
            <HomeTotalMintedNFT />
          </Grid>

          <Grid item xs={12} md={4}>
            <HomeTotalMintedNFT />
          </Grid>

          <Grid item xs={12} md={4}>
            <HomeTotalMintedNFT />
          </Grid>
        </Grid>
        <HomeSectionHeader
          heading="Development progress"
          action={
            <Button
              variant="contained"
              component={RouterLink}
              to={PATH_DASHBOARD.root}
              startIcon={<Icon icon={plusFill} />}
            >
              Your Proposal
            </Button>
          }
        />
        <Grid container spacing={3}>
          <Grid item xs={12} md={4}>
            <HomeWorkProgress title="Decentralization SwitchSwap" tasks={DECENTRALIZATION_TASKS} />
          </Grid>

          <Grid item xs={12} md={4}>
            <HomeWorkProgress title="Smart Contracts Dev" tasks={SMARTCONTRACT_TASKS} />
          </Grid>

          <Grid item xs={12} md={4}>
            <HomeWorkProgress title="Interface Design" tasks={INTERFACEDESIGN_TASKS} />
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
}
